import React, { useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { signup } from "../actions/auth";
import { Form, Row, Col, Button, Container } from "react-bootstrap";
import uiImg from '../images/login.svg'
import '../css/signup.css'

const Signup = ({ signup, isAuthenticated }) => {
  const regexp = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  const [accountCreated, setAccountCreated] = useState(false);
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    re_password: "",
    number: "",
    gender: "",
    dob: "",
  });
  const [errors, setErrors] = useState({});
  console.log(errors)

  const { username, email, password, re_password, number, gender, dob } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    if (username === ""){
      setErrors({username:'Username is Required'})
    }

    if (!email.trim()){
      setErrors({email:'Email is Required'})
      if (!regexp.test(email)) {
        setErrors({email:'Invalid Email'})
      }
    }

    if (!password.trim()){
      setErrors({password:'Password is Required'})
      if (password !== re_password) {
        setErrors({password:'Password does not match'})
      }
    }

    if (!re_password.trim()){
      setErrors({re_password:'Password Confirmation is Required'})
    }

    if (!number.trim()){
      setErrors({number:'Number is Required'})
    }

    if (!dob.trim()){
      setErrors({dob:'Date of Birth is Required'})
    }

    
    else {
        signup(username, email, password, re_password, number, gender, dob);
        setAccountCreated(true);
    }
  };

  // const continueWithGoogle = async () => {
  //     try {
  //         const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/o/google-oauth2/?redirect_uri=${process.env.REACT_APP_API_URL}/google`)

  //         window.location.replace(res.data.authorization_url);
  //     } catch (err) {

  //     }
  // };

  // const continueWithFacebook = async () => {
  //     try {
  //         const res = await axios.get(`${process.env.REACT_APP_API_URL}/auth/o/facebook/?redirect_uri=${process.env.REACT_APP_API_URL}/facebook`)

  //         window.location.replace(res.data.authorization_url);
  //     } catch (err) {

  //     }
  // };

  if (isAuthenticated) {
    return <Redirect to="/dashboard" />;
  }
  if (accountCreated) {
    return <Redirect to="/login" />;
  }

  return (
    <>
      <Container className="mt-1">
        <Row>
          <Col lg={6} md={7} sm={12}>
            <img className="w-100" src={uiImg} alt="" />
          </Col>
          <Col lg={6} md={6} sm={12} className="mt-1 p-0">
            {/* <img className="icon-img" src={loginIcon} alt="icon" /> */}
            <h1 className="title">SSK Enterprise</h1>
            <Form onSubmit={onSubmit}>
              <Form.Group className="mb-0" controlId="formGroupText">
                <Form.Label>Username</Form.Label>
                <Form.Control
                  name="username"
                  className={`input ${errors.username && "errorShow"}`}
                  type="text"
                  placeholder="Enter username"
                  value={username || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {errors.username && (
                  <p className="errorShow">{errors.username}</p>
                )}
              </Form.Group>
              <Form.Group className="mb-0" controlId="formGroupEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                  name="email"
                //   className={`input ${errors.email && "errorShow"}`}
                  type="email"
                  placeholder="Enter email"
                  value={email || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {/* {errors.email && <p className="errorShow">{errors.email}</p>} */}
              </Form.Group>
              <Form.Group className="mb-0" controlId="formGroupPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control
                  name="password"
                //   className={`input ${errors.password && "errorShow"}`}
                  type="password"
                  placeholder="Enter Password"
                  minLength='8'
                  maxLength='14'
                  value={password || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {/* {errors.password && (
                  <p className="errorShow">{errors.password}</p>
                )} */}
              </Form.Group>
              <Form.Group className="mb-0" controlId="formGroupConfirmPassword">
                <Form.Label>Confirm Password</Form.Label>
                <Form.Control
                  name="re_password"
                //   className={`input ${errors.password2 && "errorShow"}`}
                  type="password"
                  placeholder="Enter Confirm Password"
                  value={re_password || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {errors.password && (
                  <p style={{
                    fontWeight: 'bold',
                    color: 'red',
                  }}className="errorShow">{errors.password}</p>
                )}
              </Form.Group>
              <Form.Group className="mb-0" controlId="formGroupNumber">
                <Form.Label>Mobile Number</Form.Label>
                <Form.Control
                  name="number"
                //   className={`input ${errors.number && "errorShow"}`}
                  type="text"
                  placeholder="Enter Mobile Number"
                  minLength='11'
                  minLength='11'
                  value={number || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {/* {errors.number && <p className="errorShow">{errors.number}</p>} */}
              </Form.Group>
              <Row>
                <Col>
                  <Form.Group controlId="formBasicSelectGender">
                    <Form.Label>Select Gender</Form.Label>
                    <Form.Control
                      name="gender"
                    //   className={`input ${errors.gender && "errorShow"}`}
                      as="select"
                      value={gender || ""}
                      onChange={e => onChange(e)}
                    >
                      <option value="Gender">Select...</option>
                      <option value="Male">Male</option>
                      <option value="Feale">Female</option>
                      <option value="Other">Other</option>
                    </Form.Control>
                  </Form.Group>
                </Col>
                <Col>
                  <Form.Group className="mb-0" controlId="formGroupDate">
                    <Form.Label>Date of Birth</Form.Label>
                    <Form.Control
                      name="dob"
                    //   className={`input ${errors.dob && "errorShow"}`}
                      type="date"
                      placeholder="Enter Date of Birth"
                      value={dob || ""}
                      onChange={e => onChange(e)}
                      required
                    />
                    {/* {errors.dob && <p className="errorShow">{errors.dob}</p>} */}
                  </Form.Group>
                </Col>
              </Row>
              <div className="d-flex justify-content-center">
                <Button
                  as="input"
                  type="submit"
                  value="Signup"
                  className="submit"
                />
              </div>

              <div className="createAccount">
                <span>
                  Already Have an Account
                  <a href="/login" >
                    Login
                  </a>
                </span>
              </div>
              <div className="back">
                <a href="/" >
                  Back To Site
                </a>
              </div>
            </Form>
          </Col>
        </Row>
      </Container>
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { signup })(Signup);
