import axios from "axios";
import { SCRAPE_SUCCESS, SCRAPE_FAIL, DATA_UPLOAD_SUCCESS,
  DATA_UPLOAD_FAIL, } from "../actions/types";

export const scrape_twitter = (query,username,email) => async (dispatch) => {
  if (localStorage.getItem("access")) {
    const config = {
      headers: {
        "Content-Type": "application/json",
        'Authorization': `JWT ${localStorage.getItem("access")}`,
        'Accept': "application/json",
      },
    };

    const body = JSON.stringify({ 
      query: query,
      username: username,
      email: email 
    });
    console.log('body',body)
    try {
        
      const res = await axios.post("/scrappers/twitter_scrapper/", body, config);
      console.log('hello',res.data);

      dispatch({
        type: SCRAPE_SUCCESS,
        payload: res.data,
      });
    } catch (error) {
      dispatch({
        type: SCRAPE_FAIL,
        payload:
          error.response && error.response.data.detail
            ? error.response.data.detail
            : error.message,
      });
    }
  } else {
    dispatch({
      type: SCRAPE_FAIL,
    });
  }
};

