import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { reset_password } from "../actions/auth";
import { Form, Row, Col, Button, Container } from "react-bootstrap";
import uiImg from "../images/login.svg";

const ResetPassword = ({ reset_password }) => {
  const [requestSent, setRequestSent] = useState(false);
  const [formData, setFormData] = useState({
    email: "",
  });

  const { email } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    reset_password(email);
    setRequestSent(true);
  };

  if (requestSent) {
    console.log('sent')
    return <Redirect to="/login" />;
    
  } else {
    console.log("not send");
    //return <Redirect to='/login' />
  }

  return (
    <>
      <Container className="mt-1">
        <Row>
          <Col lg={8} md={8} sm={12}>
            <img className="w-100" src={uiImg} alt="" />
          </Col>
          <Col lg={4} md={8} sm={14} className="mt-1 p-0">
            {/* <img className="icon-img" src={loginIcon} alt="icon" /> */}
            <h1 className="mt-3 d-flex justify-content-center">
              SSK Enterprise
            </h1>
            <h1 className="mt-5 d-flex justify-content-center">Welcome User</h1>
            <Form onSubmit={onSubmit}>
              <Form.Group className="mb-0" controlId="formGroupEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                  name="email"
                  //   className={`input ${errors.email && "errorShow"}`}
                  type="email"
                  placeholder="Enter email"
                  value={email || ""}
                  onChange={(e) => onChange(e)}
                  required
                />
                {/* {errors.email && <p className="errorShow">{errors.email}</p>} */}
              </Form.Group>

              <div className="d-flex justify-content-center">
                <Button
                  as="input"
                  type="submit"
                  value="Reset Password"
                  className="submit"
                />
              </div>

              <div className="back">
                <a href="/">Back To Site</a>
              </div>
            </Form>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default connect(null, { reset_password })(ResetPassword);
