import React, { useState } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { login } from '../actions/auth'
import { Form, Row, Col, Button, Container } from "react-bootstrap"
import uiImg from '../images/login.svg'

const Login = ({ login, isAuthenticated }) => {
    const [formData, setFormData] = useState({
        email: '',
        password: '' 
    });

    const { email, password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        login(email, password);
        
    };

    
    if (isAuthenticated) {
        return <Redirect to='/dashboard'/>
    }
    

    return (
        <>
      <Container className="mt-1">
        <Row>
          <Col lg={8} md={8} sm={12}>
            <img className="w-100" src={uiImg} alt="" />
          </Col>
          <Col lg={4} md={8} sm={14} className="mt-1 p-0">
            {/* <img className="icon-img" src={loginIcon} alt="icon" /> */}
            <h1 className="mt-3 d-flex justify-content-center">SSK Enterprise</h1>
            <h1 className="mt-5 d-flex justify-content-center">Welcome User</h1>
            <Form onSubmit={onSubmit}>
              <Form.Group className="mb-0" controlId="formGroupEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control
                  name="email"
                //   className={`input ${errors.email && "errorShow"}`}
                  type="email"
                  placeholder="Enter email"
                  value={email || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {/* {errors.email && <p className="errorShow">{errors.email}</p>} */}
              </Form.Group>
              <Form.Group className="mb-0" controlId="formGroupPassword">
                <Form.Label>Password</Form.Label>
                <Form.Control
                  name="password"
                //   className={`input ${errors.password && "errorShow"}`}
                  type="password"
                  placeholder="Enter Password"
                  minLength='8'
                  maxLength='14'
                  value={password || ""}
                  onChange={e => onChange(e)}
                  required
                />
                {/* {errors.password && (
                  <p className="errorShow">{errors.password}</p>
                )} */}
              </Form.Group>
              <div className="d-flex justify-content-left">
                
                  <p>
                  <Link to="/reset-password">
                    Forgot Password
                    </Link>
                  </p>
                
                </div>
              <div className="d-flex justify-content-center">
                <Button
                  as="input"
                  type="submit"
                  value="Login"
                  className="submit"
                />
              </div>

              <div className="createAccount">
                <span>
                  New Here?
                  <a href="/signup" >
                    Create Account
                  </a>
                </span>
              </div>
              <div className="back">
                <a href="/" >
                  Back To Site
                </a>
              </div>
            </Form>
          </Col>
        </Row>
      </Container>
    </>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { login })(Login);